import Presentacion from "./presentacion";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faFingerprint as fa1, faJedi as fa2} from '@fortawesome/free-solid-svg-icons';
import About from "./About";
import Projects from './projects';
import { projects } from "../resources/projects";
import { herramientas } from "../resources/herramientas";
import { useCallback, useState } from "react";

function Main() {
    const [selection, setSelection] = useState(false);

    const handlerChangeSelection = useCallback((valor) => {
            setSelection(valor)
        }, [selection]);

    return (
        <div className="main">
            <Presentacion/>
            <div className="container-navigation">
            <div 
                className={selection==false ? "nav-item nav-work section-selected" : "nav-item nav-work"} 
                onClick={() => handlerChangeSelection(false)}>    
                    <FontAwesomeIcon className="icon" icon={fa1} />
                    <span>ABOUT</span>        
                </div> 
                <div 
                    className={selection==true ? "nav-item nav-work section-selected" : "nav-item nav-work"} 
                    onClick={() => handlerChangeSelection(true)}>
                        <FontAwesomeIcon className="icon" icon={fa2} />
                        <span>PROJECTS</span>        
                </div> 
            </div>
            <div className="container-pages">
                <div className="container-about">
                    {
                        selection==false ? <About listado={herramientas} /> : <Projects listado={projects}/>
                    }
                </div>
            </div>
        </div>
    )
}

export default Main;






