

export function Rol(props) {
    const {titulo, entidad, periodo} = props;
    return (
        <div className="item">
            <div className="container-titulo">
                <span className="titulo">{ titulo }</span>
            </div>
            <div className="descripcion">
                <span className="entidad">{ entidad }</span>
                <span className="periodo">{ periodo }</span>
            </div>
        </div>
    )
}

function About(props) {
    const { listado } = props;
    return (
        <div className="about-section">
            <div className="educacion about-item">
                <div className="container-titulo-seccion">
                    <h1>EDUCACIÓN</h1>
                </div>
                <Rol 
                    titulo='Tecnicatura Universitaria en Informatica' 
                    entidad='Universidad Nacional de General Sarmiento - UNGS'
                    periodo='2018 - culminada en 2021'/>
            </div>
            <div className="historial-laboral about-item">
                <div className="container-titulo-seccion">
                    <h1>HISTORIAL PROFESIONAL</h1>
                </div>
                <Rol 
                    titulo='Tec. Administrador de bases de datos y Reporting' 
                    entidad='Comisión Nacional de Regulación del Transporte - CNRT'
                    periodo='2020 - Actualidad'/>
            </div>
            <div className="container-herramientas">
                <div className="container-titulo">
                    <span className="titulo"> HERRAMIENTAS</span>
                </div>
                <div className="listado">
                    {
                        listado.map(i => (
                            <div className={"skill "+i.titulo} key={i.url}>
                                <img src={process.env.PUBLIC_URL +'/assets/skills/'+i.url} className='skill-img'/>
                                <span className="skill-nombre">{i.titulo}</span>
                            </div>
                        ))
                    }
                </div>
            </div>
        </div>
    );
}


export default About;






