import react from "react";
import Main from "./main";
import Img from "./partials/Img";
import fondo from '../images/bg7.jpg'
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import GitHubIcon from '@material-ui/icons/GitHub';
import InstagramIcon from '@material-ui/icons/Instagram';

export default function Dashboard(props) {
    const { logo, AppsItems } = props;
    return (
       <div className="dashboard">
           <Img url={fondo} containerImg='containerHeader' clase='headerImg'/>
           <Main/>
           <div className="footer">
                <div className="creditos">
                    <div className="container-email">
                        <span className="year">{(new Date).getUTCFullYear()} (c)</span>
                        <span className="email">francocisneros147@gmail.com</span>
                    </div>
                    <div className="redes-sociales">
                        <a className="item" target='_BLANK' href="https://github.com/CisnerosFranco">
                            <GitHubIcon className="item-icon"/>
                        </a>
                        <a className="item linkedin" target='_BLANK' href="https://www.linkedin.com/in/franco-cisneros-9a6a6a1a3/">
                            <LinkedInIcon className="item-icon"/>
                        </a>
                        <a className="item instagram" target='_BLANK' href="https://www.instagram.com/_frank.1/">
                            <InstagramIcon className="item-icon"/>
                        </a>
                    </div>
                </div>
           </div>
       </div> 
    )
}





