import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCircleChevronRight as fa1} from '@fortawesome/free-solid-svg-icons';

function Project(props) {
    const { img, titulo, url, descripcion } = props.item;
    return (
        <div className="project">
            <div className="container-img">
                <img src={process.env.PUBLIC_URL +'/assets/projects/'+img} className='project-img'/>
                <div className="container-section-descripcion-img">
                    <div className="container-titulo">
                        <span className="titulo">{titulo}</span>
                    </div>
                    <div className="container-descripcion">
                        <span className="descripcion">{descripcion}</span>
                    </div>
                    <div className="container-btn">
                        <a target='_blank' href={url} className='btn-ir'>
                            <FontAwesomeIcon className="icon" icon={fa1} />
                        </a>
                    </div>
                </div>
            </div>
            <div className="container-section-descripcion">
                <div className="container-titulo">
                    <span className="titulo">{titulo}</span>
                </div>
                <div className="container-descripcion">
                    <span className="descripcion">{descripcion}</span>
                </div>
                <div className="container-btn">
                    <a target='_blank' href={url} className='btn-ir'>
                        <FontAwesomeIcon className="icon" icon={fa1} />
                    </a>
                </div>
            </div>
        </div>
    )
}

function Projects(props) {
    const { listado } = props;

    return (
        <div className="projects-section">
           
            <div className="container-projects">
                {
                    listado.map(i => <Project key={i.titulo} item={i} />)
                }
            </div>
        </div>  
    )
}


export { Project };
export default Projects;

