import React, { useState } from 'react';
import Dashboard from './components/dashboard';
import logo from './images/bg7.jpg';
import './styles/App.scss';
import './styles/About.scss';
import './styles/Projects.scss'

function App() {
    const [AppsItems, setAppsItems] = useState(['PLP/SQL', 'REACT', 'NODE.JS']);

    return (
        <div className="app">
            <Dashboard AppsItems={AppsItems} logo={logo}/>        
        </div>
    )
}

export default App;





