import react from "react";
import Img from "./partials/Img";
import logo from '../images/foto1.png'


function Presentacion(props) {
    return(
        <div className="presentacion">
           <Img url={logo} containerImg='container-logo' clase='foto-logo'/>
           <div className="header">
                <h1 className="titulo">Franco Cisneros</h1>
                <p className="sub-titulo">React Developer - DBA</p>
           </div>
           <div className="container-msg-principal">
                <p>Tecnico universitario en Informatica, graduado de la Universidad Nacional de General Sarmiento (UNGS).</p>
                <p>Me dedico al desarrollo Web full stack y Administración de bases de datos en paradigma relacional y no relacional.</p>
                <p>Un joven entusiasta, con sueños y objetivos muy claros.</p>
           </div>
           
        </div>
    )
}

export default Presentacion;

